	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Software gets complicated fast. Most of good architecture and design practise is about trying to slow the rate at which software gets complicated. You can’t stop it, it’s a form of entropy. You can only slow it down and do your level best to stay on top of things.\n\nOne way to manage the mess is to maximise the likelihood that everyone knows what’s going on in the codebase. This requires two things: consistency and replaceability. Consistency implies you can make reasonable assumptions about unfamiliar parts of the application. Replaceability means you can kill code easily and replace it with something better.\n\nDan North argues that code should either be new enough that someone remembers writing it, or well-enough established that everyone knows how it works. It’s code in that awkward middle stage, between brand new and part-of-the-furniture, that gets forgotten about and starts to smell. If code is going to die it should die quickly. If it is going to stick around it should be stable.\n\nIn this talk, Dan describes a model for thinking about the age of code and argues for replaceability as a first class concern. He also discovers that if you optimise for both replaceability and consistency you can end up with something that looks a lot like microservices.","meta":{"duration":"PT37M22S"},"presenter":{"name":"Dan North"},"source":{"id":"4Y0tOi7QWqM","provider":"youtube"},"slug":"software-that-fits-in-your-head","tag":["Agile","GOTO Conference","Mircoservices","Software Architecture"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/4Y0tOi7QWqM/default.jpg","height":90,"width":120}},"title":"Software that Fits in Your Head"}},{"node":{"description":"Some teams are orders of magnitude more effective than others, turning around business solutions in days or even hours. Their secret is a combination of smart technology choices, great development habits and a powerful team dynamic. In this talk Dan describes a number of patterns of behaviour that he's identified working with some great teams, beyond the basics of co-location, stand-ups and pair rotation. You'll gain a new appreciation for old techniques like code reviews, and even working in silos won't seem so bad!","meta":{"duration":"PT51M4S"},"presenter":{"name":"Dan North"},"source":{"id":"lvs7VEsQzKY","provider":"youtube"},"slug":"patterns-of-effective-teams","tag":["GOTO Conference","Agile"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/lvs7VEsQzKY/default.jpg","height":90,"width":120}},"title":"Patterns of Effective Teams"}}]}},"pathContext":{"speaker":"Dan North"}}

